.rdtPicker{padding:0 12px;-webkit-box-shadow:none;box-shadow:none;border:1px solid #ccc;border-radius:0 0 3px 3px;color:#585858;position:inherit;width:auto}.rdtPicker table{border-spacing:0}.rdtPicker thead td{padding-left:0;padding-right:0}.rdtPicker thead tr:first-child th{border-bottom:1px solid #ccc;padding:4px 0 5px}.rdtPicker td,.rdtPicker th{line-height:19px;height:19px;font-size:14px}.rdtPicker th{padding:3px 2px 2px}.rdtPicker td{padding:8px}.rdtPicker td:hover{border-radius:3px}.rdtPicker td.rdtActive,.rdtPicker td.rdtActive:hover{background:#52b6ca;border-radius:3px}.rdtPicker td.rdtToday:before{border-bottom-color:#112a46}.rdtDays thead tr:last-child th{padding-top:15px}.rdtYears table:last-child{padding:5px}.rdtCounters .rdtCount{font-size:14px}